import { Button, Card, Col, Container, Row } from "react-bootstrap";
import { LinkContainer } from "react-router-bootstrap";
import GetData from "../GetData";
import * as photoData from "../../data/blendsWellWith/photoData";

export default function BlendsWellWithCard(props) {
  return (
    <Col>
      <Card
        style={{
          "text-align": "center",
          "min-height": "100px",
          "margin-bottom": "1%"
        }}
      >
        <Card.Img
          style={{ "max-height": "100px", "object-fit": "cover" }}
          src={photoData.photoData[props.oil[0]]}
        />
        <Card.Body>
          <Card.Title>{props.oil[0]}</Card.Title>
          <Card.Text>{props.oil[1]}</Card.Text>
        </Card.Body>
      </Card>
    </Col>
  );
}
