import HomeCard from "../../components/HomeCard";
import { Container, Row, Col } from "react-bootstrap";

export default function Home() {
  return (
    <div>
      <img
        style={{ width: "100%", "object-fit": "cover", "max-height": "200px" }}
        src="https://i0.wp.com/post.healthline.com/wp-content/uploads/2021/11/lavendar-1296x728-header.jpg?w=1155"
        alt=""
      />
      <h1
        style={{
          "text-align": "center",
          "padding-top": "20px",
          "padding-bottom": "20px",
          color: "white"
        }}
      >
        Navigate to:
      </h1>
      <Container style={{ "padding-left": "5%", "padding-right": "5%" }}>
        <Row>
          <Col lg="6" md="12" sm="12">
            <HomeCard
              cardTitle="Site Map"
              linkTo="/map"
              cardText="A Map of the Farm"
              cardImageSrc="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT6hSZUsCgOVh-3upjjuMT-S5IryZy_7ZJtEQ"
            />
          </Col>{" "}
          <Col lg="6" md="12" sm="12">
            <HomeCard
              cardTitle="Plants"
              linkTo="/plants"
              cardText="View all our Plants"
              cardImageSrc="https://img.crocdn.co.uk/images/products2/pl/00/00/00/40/pl0000004047.jpg"
            />
          </Col>
        </Row>
        <Row>
          <Col lg="6" md="12" sm="12">
            <HomeCard
              cardTitle="Process"
              linkTo="/process"
              cardText="Learn about our Processes"
              cardImageSrc="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSphs8B3C8sGpNpdmz_-6cyLqMFh1-HkkFfTg"
            />
          </Col>
          <Col lg="6" md="12" sm="12">
            <HomeCard
              cardTitle="Our Products"
              linkTo="/products"
              cardText="The Story Behind our Products"
              cardImageSrc="https://pyxis.nymag.com/v1/imgs/957/9ae/68c6924a91715c39dc55ffcd8cb9348e4b-soap-lede.rsocial.w1200.jpg"
            />
          </Col>
        </Row>
      </Container>
    </div>
  );
}
