import { Navbar, Container, NavLink, Nav, NavDropdown } from "react-bootstrap";
import { Link } from "react-router-dom";
import { useState } from "react";

function SLONavbar() {
  const [expanded, setExpanded] = useState(false);

  return (
    <Navbar
      variant="light"
      className="SLONavBar"
      expand="lg"
      staticTop="true"
      expanded={expanded}
      fixed="top"
      style={{
        "background-color": "white",
        position: "sticky",
        top: 0,
        "box-shadow": "0px 0px 10px black"
      }}
    >
      <Container>
        <Navbar.Brand as={Link} to="/">
          <div>
            <img
              src="https://cdn.shopify.com/s/files/1/0495/5216/2971/files/Scottish_Lavender_Oils_Website_Header_500x.png?v=1635342911"
              height="50"
              alt="Scottish Lavender Oils Logo"
            />
          </div>
        </Navbar.Brand>
        <Navbar.Toggle
          aria-controls="basic-navbar-nav"
          onClick={() => setExpanded(expanded ? false : "expanded")}
        />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link onClick={() => setExpanded(false)} as={Link} to="/">
              Home
            </Nav.Link>
            <Nav.Link onClick={() => setExpanded(false)} as={Link} to="/map">
              Site Map
            </Nav.Link>
            <Nav.Link onClick={() => setExpanded(false)} as={Link} to="/plants">
              Plants
            </Nav.Link>
            <Nav.Link
              onClick={() => setExpanded(false)}
              as={Link}
              to="/process"
            >
              Our Process
            </Nav.Link>

            <Nav.Link
              onClick={() => setExpanded(false)}
              as={Link}
              to="/products"
            >
              Our Products
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default SLONavbar;
