import * as plantData from "../../data/plants/plantData";
import { Button, Card, Container, Row, Col } from "react-bootstrap";

import PlantsCard from "../../components/PlantsCard";
import ReturnButton from "../../components/ReturnButton";

export default function Plants() {
  const PlantsArray = Object.keys(plantData.plantData);
  const PlantList = PlantsArray.map((plant) => <PlantsCard plant={plant} />);

  return (
    <div>
      <div style={{ padding: "5%" }}>
        <ReturnButton returnTo="/" textToDisplay="Home" />
      </div>
      <div style={{ color: "white", "text-align": "center", margin: "5px" }}>
        <h1>Plants</h1>
        <p>Learn about the range of aromatic plants on our farm.</p>
      </div>
      <Container style={{ "padding-left": "3%", "padding-right": "2.5%" }}>
        <Row>{PlantList}</Row>
      </Container>
      <div style={{ padding: "5%" }}>
        <ReturnButton returnTo="/" textToDisplay="Home" />
      </div>{" "}
    </div>
  );
}
